import { mapActions, mapGetters } from 'vuex';
import icons from '../icons';
import formatMixin from '../mixins/formatMixin';

export default {
  name: 'MemberGroups',
  mixins: [formatMixin],
  data() {
    return {
      tab: 'groups',
      loading: false,
      memberFilter: '',
      groupFilter: '',
      memberPagination: {
        sortBy: 'desc',
        descending: false,
        rowsPerPage: this.$q.screen.xs ? 3 : 8,
      },
      groupPagination: {
        sortBy: 'desc',
        descending: false,
        rowsPerPage: this.$q.screen.xs ? 4 : 8,
      },
    };
  },
  methods: {
    ...mapActions('tools', ['getMemberGroups']),
  },
  mounted() {
    this.loading = true;
    this.getMemberGroups()
      .then(() => {
        this.loading = false;
      });
  },
  computed: {
    ...mapGetters('tools', ['groupList', 'memberList']),
    icons() {
      return icons;
    },
  },
};
